import React from "react";
import Banner from "./Components/Banner/Banner";
import Footer from "./Components/Footer/Footer";
import Navbar from "./Components/Navbar/Navbar";
import RowPoster from "./Components/RowPoster/RowPoster";
import {action,ComedyMovies,Documentaries,RomanceMovies,HorrorMovies } from "./Constants/Constants";
import "./Inner.css";
function App() {
   let number = ()=> {
      return Math.floor(Math.random() * (15 - 1 + 1)) + 1;
   }
   let randomNum = number();

   return (
      <div className="App">
         <Navbar />
         <Banner number={randomNum}/>
         <div className="inner">
            <RowPoster title="Action Movies" genre={action} isLarge/>
            <RowPoster title="Comedy Movies" genre={ComedyMovies}/>
            <RowPoster title="Documentaries Movies" genre={Documentaries}/>
            <RowPoster title="Romance Movies" genre={RomanceMovies}/>
            <RowPoster title="Horror Movies" genre={HorrorMovies}/>
            <Footer/>
         </div>
      </div>
   );
}

export default App;
