import React, { useEffect, useState } from "react";
import axios from "../../axios";
import YouTube from 'react-youtube';
import { API_KEY, imageUrl } from "../../Constants/Constants";
import "./RowPoster.css";
function RowPoster(props) {
  
   const [movies, setMovies] = useState([]);
   const [videoUrl, setVideoUrls] = useState('')
   useEffect(() => {
      axios
         .get(props.genre)
         .then((movielist) => {
            setMovies(movielist.data.results);
         })
         .catch((err) => {
            console.log("Network Error");
         });
   });

   const showYoutubeVideo = (id)=>{
      axios.get(`https://api.themoviedb.org/3/movie/${id}/videos?api_key=${API_KEY}&language=en-US`).then((response)=>{
         if (response.data.results.length !== 0) {
            
            setVideoUrls(response.data.results[0])
         }
      })
   }

   const opts = {
      height: '390',
      width: '100%',
      playerVars: {
        // https://developers.google.com/youtube/player_parameters
        autoplay: 0,
      },
    };

   return (
      <div className="posterbody">
         <h2 className="categoryTitle ms-5">{props.title}</h2>
         <div className="posters ms-4">
            {movies.map((movie,index) => (
               <img key={index} onClick={()=>showYoutubeVideo(movie.id)} className={ props.isLarge ? 'largeposterImg' : 'posterImg'} src={imageUrl + movie.backdrop_path} alt="posterImg" />
            ))}
         </div>
         <div className="modal fade" id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div className="modal-dialog">
    <div className="modal-content">
      <div className="modal-header">
        <h1 className="modal-title fs-5" id="exampleModalLabel">Modal title</h1>
        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div className="modal-body">
        ...
      </div>
      <div className="modal-footer">
         <button>Reload Button</button>
        <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        <button type="button" className="btn btn-primary">Save changes</button>
      </div>
    </div>
  </div>
</div>
         {videoUrl && <YouTube videoId={videoUrl.key} opts={opts}  />}
      </div>
   );
}

export default RowPoster;
