import React, { useEffect, useState } from "react";
import axios from "../../axios";
import { imageUrl, trending } from "../../Constants/Constants";
import "./Banner.css";
function Banner(props) {
   const [movie, setMovie] = useState();

   useEffect(() => {
      axios.get(trending).then((movie) => {
         setMovie(movie.data.results[props.number]);
      });
   });

   return (
      <div className="banner" style={{ backgroundImage: `url(${movie ? imageUrl + movie.backdrop_path : null})` }}>
         <div className="movieDetails">
            <h3 className="head">{movie ? movie.title : null}</h3>
            <p className="description w-25">{movie ? movie.overview : null}</p>
            <div className="buttonSection">
               <button className="bannerButtons playButton btn btn-secondary d-flex align-items-center">
                  <span>
                     <img className="me-3 ms-3 mt-1" src="./play-button-arrowhead.png" alt="" />
                  </span>
                  <span className="fs-4 mt-1 me-2">Play</span>
               </button>
               <button className="bannerButtons d-flex align-items-center infoButton btn me-3 ms-3 mt-1">
                  <span>
                     <img className="infoImage me-3 ms-3 mt-1" src="./information.png" alt="" />
                  </span>
                  <span className="fs-4 mt-1 me-2">More Info</span>
               </button>
            </div>
         </div>
         
         <div className="blackShade"></div>
      </div>
   );
}

export default Banner;
